  #bound .hanger {
    position: relative;
    max-width: 500px;
    width: 100%;
    margin: 10px auto;
    height: 20px;
    z-index: 2002;
    top: 10px;
  }
  #bound .hanger {
    position: relative;
    max-width: 500px;
    width: 100%;
    margin: 10px auto;
    height: 20px;
    z-index: 2002;
    top: 10px;
  }
  #bound .hanger:after {
    position: absolute;
    content: "hanger: 50%";
    font-weight: bold;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
  }
  #bound .hanger:before {
    position: absolute;
    content: "";
    font-weight: bold;
    top: 100%;
    left: 50%;
    background: #333;
    width: 2px;
    height: 25px;
  }
  #bound .anchor {
    position: absolute;
    font-weight: bold;
    bottom: 5px;
    left: 50%;
    background: #333;
    width: 2px;
    height: 25px;
    z-index: 10;
  }
  #bound .anchor:after {
    position: absolute;
    content: "anchor: 50%";
    font-weight: bold;
    left: -40px;
    white-space: nowrap;
    bottom: 100%;
  }
  #bound .hanger:after {
    position: absolute;
    content: "hanger: 50%";
    font-weight: bold;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
  }
  #bound .hanger:before {
    position: absolute;
    content: "";
    font-weight: bold;
    top: 100%;
